import React, { useState } from "react";
import design from '../Friends.module.css';

class Pagination extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            portionNumber: 1
        }
    }

    render() {
        const pages = [];
        const pagesQuantity = Math.ceil(this.props.allUsers / this.props.usersForPage);
        for (var i = 1; i <= pagesQuantity; i++) {
            pages.push(i);
        }
        const elPerPage = 5;
        const allQuantity = Math.ceil(pagesQuantity / elPerPage);
        const startPageNumber = ((this.state.portionNumber - 1) * elPerPage + 1);
        const endPageNumber = (this.state.portionNumber * elPerPage);
        return (
            <div className={design.friends} >
                {this.state.portionNumber > 1 &&
                    <button className={design.prevPageButton} onClick={() => {
                        this.setState ({
                            portionNumber: this.state.portionNumber - 1
                        })
                    }}></button>}
                {pages
                    .filter(p => p >= startPageNumber && p <= endPageNumber)
                    .map(page => (
                        <span key={page} className={this.props.currentPage !== page && design.page|| this.props.currentPage === page &&
                            design.selectedPage}
                            onClick={() => { this.props.onPageChanged(page) }}>
                            {page}
                        </span>
                    )
                    )}
                {this.state.portionNumber < allQuantity &&
                    <button className={design.nextPageButton} onClick={() => {
                        this.setState ({
                            portionNumber: this.state.portionNumber + 1
                        })
                    }}></button>}
            </div >
        )
    }
}

export default Pagination;