import React from "react";
import design from './Friends.module.css';
import { NavLink } from 'react-router-dom'

class Friends extends React.Component {
    render() {
        return (
            <div className={design.friends} >
                {this.props.error ?
                    <div className={design.error}>
                        {this.props.error}
                    </div>
                    : ""
                }
                {
                    this.props.friendsList.map(friend =>
                        <div key={friend.id} className={design.friend} >
                            <NavLink to={'/profile/' + friend.id}>
                                <div className={design.friendPhoto}>
                                    <img src={friend.photos.small ?
                                        friend.photos.small :
                                        'https://www.pngall.com/wp-content/uploads/5/Profile-PNG-Free-Image.png'}
                                        alt="" />
                                </div>
                                <div className={design.friendName}>{friend.name}</div>
                            </NavLink>
                            <div className={design.friend_button}>
                                {friend.followed ?
                                    <button className={design.friend__unfollow_button}
                                        disabled={this.props.buttonRequest.some(
                                            id => id === friend.id)}
                                        onClick={() => { this.props.FollowUnfollowRequest(friend.id, false) }}>
                                        Unfollow</button> :
                                    <button className={design.friend__follow_button}
                                        disabled={this.props.buttonRequest.some(id => id === friend.id)}
                                        onClick={() => { this.props.FollowUnfollowRequest(friend.id, true) }}>Follow</button>}</div>
                        </div>
                    )
                }
                {this.props.PaginationContainer}
            </div >
        )
    }
}

export default Friends;