import React from "react";
import { connect } from "react-redux";
import Pagination from "./Pagination";
import {getFriends} from '../../redux/friends-reducer';

class PaginationContainer extends React.Component{
    onPageChanged = (pageNumber) => {
            this.props.getFriends(this.props.usersForPage, pageNumber)    
    }
    render(){
        return(
            <Pagination allUsers={this.props.allUsers} usersForPage={this.props.usersForPage} currentPage={this.props.currentPage} onPageChanged={this.onPageChanged} />
        )
    }
}

const mapStateToProps = (state) => {
    return {    
        allUsers: state.friendsPage.allUsers,
        usersForPage: state.friendsPage.usersForPage,
        currentPage: state.friendsPage.currentPage
    }
}

export default connect(mapStateToProps, {getFriends})(PaginationContainer)  ;