export const getFriendsList = (state) => {
    return state.friendsPage.friendsList
} 

export const getAllUsers = (state) => {
    return state.friendsPage.allUsers
} 

export const getUsersForPage = (state) => {
    return state.friendsPage.usersForPage
}

export const getCurrentPage = (state) => {
    return state.friendsPage.currentPage
}

export const getButtonRequest = (state) => {
    return state.friendsPage.buttonRequest
}

export const getError = (state) => {
    return state.app.error;
}